@font-face {
  font-family: RobotoCondensed;
  src: url(/my-portfolio/static/media/roboto-condensed.light.cf4449c1.ttf);
  font-weight: 800;
}

@font-face {
  font-family: EconomicaBold;
  src: url(/my-portfolio/static/media/economica-bold.82620571.ttf);
}

body {
  text-align: center;
  font-size: 20px;
  padding: 2.5%;
  font-family: "RobotoCondensed";
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "EconomicaBold";
}

.profile {
  width: 200px;
  height: 200px;
  border-radius: 100px;
}
.title-fade-out {
  opacity: 0;
  -webkit-animation-name: fade-out;
          animation-name: fade-out;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.title-fade-in {
  opacity: 100;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes fade-out {
  from {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

@keyframes fade-out {
  from {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

@font-face {
  font-family: RobotoCondensed;
  src: url(/my-portfolio/static/media/roboto-condensed.light.cf4449c1.ttf);
